const ScrollTop = () => {
  let scrollTop = 0,
    bodyScrollTop = 0,
    documentScrollTop = 0
  if (document.body) {
    bodyScrollTop = document.body.scrollTop
  }
  if (document.documentElement) {
    documentScrollTop = document.documentElement.scrollTop
  }
  scrollTop = bodyScrollTop - documentScrollTop > 0 ? bodyScrollTop : documentScrollTop
  return scrollTop
}

//

const ScrollHeight = () => {
  let scrollHeight = 0,
    bodyScrollHeight = 0,
    documentScrollHeight = 0
  if (document.body) {
    bodyScrollHeight = document.body.scrollHeight
  }
  if (document.documentElement) {
    documentScrollHeight = document.documentElement.scrollHeight
  }
  scrollHeight = bodyScrollHeight - documentScrollHeight > 0 ? bodyScrollHeight : documentScrollHeight
  return scrollHeight
}

//

const WindowHeight = () => {
  let windowHeight = 0
  if (document.compatMode === 'CSS1Compat') {
    windowHeight = document.documentElement.clientHeight
  } else {
    windowHeight = document.body.clientHeight
  }
  return windowHeight
}

const toBottom = () => {
  if (/(Android.+Chrome)|((iPhone|iPad).+(Safari|Chrome))/.test(navigator.userAgent))
    return Math.abs(ScrollTop() + WindowHeight() - ScrollHeight()) < 55
  return Math.abs(ScrollTop() + WindowHeight() - ScrollHeight()) < 3
}

const formatTime = data => {
  const date = new Date(data)
  const time = new Date().getTime() - date.getTime() //- =  = 
  if (time < 0) {
    return ''
  } else if (time / 1000 < 60) {
    return ''
  } else if (time / 60000 < 60) {
    return parseInt(time / 60000, 10) + ''
  } else if (time / 3600000 < 24) {
    return parseInt(time / 3600000, 10) + ''
  } else if (time / 86400000 < 31) {
    return parseInt(time / 86400000, 10) + ''
  } else if (time / 2592000000 < 12) {
    return parseInt(time / 2592000000, 10) + ''
  } else {
    return parseInt(time / 31536000000, 10) + ''
  }
}
const setCookie = (name, value) => {
  let Days = 999
  let exp = new Date()
  exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000)
  document.cookie = name + '=' + escape(value) + ';expires=' + exp.toGMTString()
}

//cookies
const getCookie = name => {
  let arr,
    reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)')
  arr = document.cookie.match(reg)
  if (arr) return unescape(arr[2])
  else return null
}

//cookies
const delCookie = name => {
  let exp = new Date()
  exp.setTime(exp.getTime() - 1)
  let cval = getCookie(name)
  if (cval != null) document.cookie = name + '=' + cval + ';expires=' + exp.toGMTString()
}

const throttle = function(func, wait, options) {
  //
  /* options
   *  funcwaitfunc
   *  options.leading = true;
   * waitfuncwaitfunc
   *  options.trailing = true; 
   * options.trailing = false
   */
  let context, args, result
  let timeout = null
  let previous = 0
  if (!options) options = {}
  let later = function() {
    previous = options.leading === false ? 0 : new Date().getTime()
    timeout = null
    result = func.apply(context, args)
    if (!timeout) context = args = null
  }
  return function() {
    let current = new Date().getTime()
    if (!previous && options.leading === false) previous = current
    // 
    let remaining = wait - (current - previous)
    context = this
    args = arguments
    // waitfunc
    // remaining <= 0waitfunc
    if (remaining <= 0 || remaining > wait) {
      // setTimeoutwaitsetTimeoutsetTimeout
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      previous = current
      result = func.apply(context, args)
      if (!timeout) context = args = null
    } else if (!timeout && options.trailing !== false) {
      // options.trailing=truefunc
      timeout = setTimeout(later, remaining)
    }
    return result
  }
}
const debounce = function(func, wait, immediate) {
  //waitdebounce
  // immediatefalse
  let timeout, args, context, timestamp, result

  let later = function() {
    // wait_.debouncetimestamplast < wait && last >= 0truefunc
    let last = new Date().getTime() - timestamp

    if (last < wait && last >= 0) {
      timeout = setTimeout(later, wait - last)
    } else {
      timeout = null
      if (!immediate) {
        result = func.apply(context, args)
        if (!timeout) context = args = null
      }
    }
  }

  return function() {
    context = this
    args = arguments
    timestamp = new Date().getTime()
    // immediatetruefunc
    let callNow = immediate && !timeout
    // waitfunc
    if (!timeout) timeout = setTimeout(later, wait)
    if (callNow) {
      result = func.apply(context, args)
      context = args = null
    }
    return result
  }
}
export {toBottom, getCookie, setCookie, delCookie, ScrollTop, formatTime, throttle, debounce}



